.toolbar-icon {
    padding: 7px 15px;
    width: 50px;
    height: 50px;
    cursor: pointer;
    float: left;
}

.toolbar-container {
    border-bottom: 1px solid;
    width: 21cm;
    margin: 0 75px;
    height: 64px
}

.toolbar-icon:hover {
    background: #e18a07;
    border-radius: 5px;
}